html{
  position: relative
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url("https://static.vecteezy.com/system/resources/previews/000/094/491/original/polygonal-texture-background-vector.jpg");
  background-repeat: no-repeat, repeat;
  background-position: center;
  background-size: cover;
  margin: 10px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}





#connectLogo {
  height: 60px;
  padding: 15px 0 5px 0;
}
#logo {
  height: 60px;
  padding: 5px 0 5px 20px;
}

.share-link {
  line-height: 60px;
  padding: 0 1em;
  font-size: 2em;
}




#wrapper {
  padding-left: 0;
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

#wrapper.toggled {
  padding-left: 250px;
}

#sidebar-wrapper {
  /* z-index: 1000; */
  position: fixed;
  left: 250px;
  width: 0;
  height: 100%;
  margin-left: -240px;
  bottom: -150px;
  overflow-y: auto;
  background: #000;
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

#wrapper.toggled #sidebar-wrapper {
  width: 250px;
}

#page-content-wrapper {
  width: 100%;
  position: absolute;
  padding: 15px;
  background-color: rgba(255,255,255,1);
  opacity: 0.9;
  color: #000;
  bottom: 12px;
  position: relative;
}

#wrapper.toggled #page-content-wrapper {
  position: absolute;
  margin-right: -250px;
}

/* nav */

.navbar{
  background-color: rgba(255,255,255,1);
  opacity: 0.9;
  color: #000;
}
/* Sidebar Styles */

.sidebar-nav {
  position: absolute;
  top: 0;
  width: 250px;
  margin: 0;
  padding: 0;
  list-style: none;
}

.sidebar-nav li {
  text-indent: 20px;
  line-height: 40px;
}

.sidebar-nav li a {
  display: block;
  text-decoration: none;
  color: #999999;
}

.sidebar-nav li a:hover {
  text-decoration: none;
  color: #fff;
  background: rgba(255,255,255,0.2);
}

.sidebar-nav li a:active,
.sidebar-nav li a:focus {
  text-decoration: none;
}

.sidebar-nav > .sidebar-brand {
  height: 65px;
  font-size: 18px;
  line-height: 60px;
}

.sidebar-nav > .sidebar-brand a {
  color: #999999;
}

.sidebar-nav > .sidebar-brand a:hover {
  color: #fff;
  background: none;
}

@media(min-width:768px) {
  #wrapper {
      padding-left: 250px;
  }

  #wrapper.toggled {
      padding-left: 0;
  }

  #sidebar-wrapper {
      width: 239px;
  }

  #wrapper.toggled #sidebar-wrapper {
      width: 0;
  }

  #page-content-wrapper {
      padding: 20px;
      position: relative;
  }

  #wrapper.toggled #page-content-wrapper {
      position: relative;
      margin-right: 0;
  }
}

/* logo */

.logo{
  height: 1000px;
}

.brand{
  height: 300px;
  padding-left: 0;
  padding-right: 0;
  padding-bottom: 1px;
  padding-top: 1px;
  background-repeat: no-repeat;
  background-position: top center;
  background-size: auto 300px;
}

#root{
  text-align:center;
  padding:1px;
  margin:auto;
  background-color: rgba(255,255,255,1);
  opacity: 0.9;
}

.puntos{
  list-style-type: disc;
}

/* footer */
.page-footer{
  background-color: aquamarine;
  color: #000;
  position:fixed;
  left:0px;
  bottom:0px;
  height:40px;
  width:100%;
}

.footer-copyright{
  padding-top: 1rem;
}







